import { BrowserRouter as Router, Route, Switch, Link, withRouter} from 'react-router-dom'
import React, { Component } from 'react';
import { SearchInput, CategoryInput } from './App.js';

export class HomePage extends Component {

    constructor (props) {
        super (props);
        this.state = {
            error: false
        };
    }

    componentDidMount () {
        this.props.restart ();
    }

    onClick = () => {
        if (this.props.categoryInput == "none" && this.props.inputValue == "") {
            this.setState ({error: true});
        } else {
            this.setState ({error: false});
            let path = "/searchresults=" + this.props.inputValue;
            if (this.props.categoryInput != "none") {
                path = "/category=" +this.props.categoryInput;
            }
            this.props.history.push(path);
        }
    }

    render () {
        let show = "d-none";
        if (this.state.error) {
            show = "";
        }

        return (
        <main className="container">
            <section id="search-home">
                <p id="alert" className={"alert alert-danger text-center " + show}>Please type in what your are trying to find OR select a category.</p>
                <SearchInput 
                    changeInput={(query) => this.props.changeInput (query)} 
                    inputValue={this.props.inputValue}
                    categoryInput={this.props.categoryInput}
                    path={this.props.match} />
                <p className="text-center">or</p>
                <CategoryInput 
                    category={this.props.category} 
                    searchInput={this.props.inputValue}
                    categoryInput={this.props.categoryInput}
                    changeCategory={(category) => this.props.changeCategory(category)} />
                <button onClick={this.onClick} id="search-btn" className="btn btn-outline-primary btn-block">Search</button>
                {/* <p className="text-center mt-3 text-secondary small">
                    Note: To view the resistor values we have, please select the <em>Resistors</em>
                    category and then the search button. This will download a resistor catalog file.
                </p> */}
            </section>
        </main>
        );

    }

}

export default withRouter (HomePage);
