import React, { Component } from 'react';
import 'bootstrap/dist/css/bootstrap.min.css';
import * as d3 from "d3";
import data from './data/ece-catalog.csv';
import resistorData from './data/resistors.csv';
import firebase from 'firebase/app';
import 'firebase/database';
import { BrowserRouter as Router, Route, Switch, Link, withRouter } from 'react-router-dom'
import { TabContent, TabPane, Nav, NavItem, NavLink, Card, Button, CardTitle, CardText, Row, Col } from 'reactstrap';
import classnames from 'classnames';
import './App.css';
import {TinyButton as ScrollUpButton} from "react-scroll-up-button"; //Add this line Here

import { HomePage } from './HomePage.js'

class App extends Component {

  constructor(props) {
    super(props);
    this.state = {
      data: [],
      loading: true,
      error: "",
      query: "",
      category: "none",
      admin: false
    }
  }

  componentDidMount() {

    d3.csv(data)
      .then((catalogData) => {
        console.log(catalogData);
        this.setState({ data: catalogData, loading: false });
        firebase.database().ref('catalog')
          .set(catalogData)
          .catch((error) => {
            console.log(error);
          });

      })
      .catch((error) => {
        this.setState({ error: error.message });
        console.log(error);
      });
  }

  restart = () => {
    this.setState({ query: "", category: "none" });
  }

  changeInput = (input) => {
    this.setState({ query: input });
    console.log(input)
  }

  changeCategory = (selectedCategory) => {
    console.log(selectedCategory)
    this.setState({ category: selectedCategory });
  }

  generateCategories = () => {

    let categories = ["Resistors"];
    if (this.state.data) {
      this.state.data.forEach((item) => {
        if (categories.indexOf(item.Category) === -1) {
          categories.push(item.Category)
        }
      });
    }
    return (categories);

  }

  renderHomePage = (routerProps) => {
    return (
      <HomePage
        {...routerProps}
        category={this.generateCategories()}
        changeInput={this.changeInput}
        inputValue={this.state.query}
        categoryInput={this.state.category}
        changeCategory={this.changeCategory}
        restart={this.restart} />
    );
  }

  renderSearchResults = (routerProps) => {
    return (
      <SearchResults
        {...routerProps}
        admin={this.state.admin}
        data={this.state.data}
        changeInput={this.changeInput}
        inputValue={this.state.query}
        categoryInput={this.state.category}
        restart={this.restart} />
    );
  }

  renderFooter = (routerProps) => {
    return (
      <Footer
        {...routerProps}
        admin={this.state.admin}
        changeAdmin={this.changeAdmin} />
    );
  }

  changeAdmin = () => {
    this.setState((currentState) => {
      return ({ admin: !currentState.admin });
    });
  }

  renderResistor = (routerProps) => {
    return (
      <Resistor 
        {...routerProps}
        changeInput={this.changeInput}
        inputValue={this.state.query}
        categoryInput={this.state.category}
         />
    );
  }

  render() {
    if (!this.state.loading) {
      return (
        <Router>
          <div>
            <Route path="/" component={Header} />
            <Switch>
              <Route exact path="/" render={this.renderHomePage} />
              <Route exact path="/searchresults=:query" render={this.renderSearchResults} />
              <Route exact path="/category=resistors" render={this.renderResistor} />
              <Route exact path="/category=:category" render={this.renderSearchResults} />
            </Switch>
            <Route path="/" render={this.renderFooter} />
          </div>

        </Router>
      );
    } else {
      return (
        <p>Loading Content</p>
      );
    }
  }
}

export default App;

class Resistor extends Component {

  constructor(props) {
    super(props);
    this.state = {
      resistorData: [],
      loading: true,
      activeTab: '1'
    }

    this.toggle = this.toggle.bind(this);
  }

  toggle(tab) {
    if (this.state.activeTab !== tab) {
      this.setState({
        activeTab: tab
      });
    }
  }

  componentDidMount() {
    d3.csv(resistorData)
      .then((data) => {
        this.setState({ resistorData: data, loading: false });
        console.log(data);
      })
      .catch((error) => {
        console.log(error.message);
      });
  }

  resistorValues = () => {
    let resistorValue = [];
    this.state.resistorData.forEach((item) => {
      if (resistorValue.indexOf(item.Type) == -1) {
        resistorValue.push(item.Type);
      }
    });
    return (resistorValue);
  }

  filterResistorData = (filter) => {
    let specificResistor = this.state.resistorData.filter((item) => {
      return (item.Type == filter);
    });
    return (specificResistor);
  }

  render() {

    if (this.state.loading) {
      return (null);
    } else {

      let locator = <p><Link to="/">Home</Link> &gt; Resistors</p>;

      let resistorTabLabel = this.resistorValues();
      let tabCount = 0;

      return (
        <div className="container">
          {locator}
          <SearchInput
              changeInput={(query) => this.props.changeInput(query)}
              inputValue={this.props.inputValue}
              categoryInput={"none"}
              path={this.props.match} />
          <h2 className="text-center">Resistors</h2>
          <Nav tabs>
            <NavItem>
              <NavLink
                className={classnames({ active: this.state.activeTab === "1" })}
                onClick={() => { this.toggle("1"); }}
              >
                Search for a Resistor
                </NavLink>
            </NavItem>
            <NavItem>
              <NavLink
                className={classnames({ active: this.state.activeTab === "2" })}
                onClick={() => { this.toggle("2"); }}
              >
                {resistorTabLabel[0]}
              </NavLink>
            </NavItem>
            <NavItem>
              <NavLink
                className={classnames({ active: this.state.activeTab === "3" })}
                onClick={() => { this.toggle("3"); }}
              >
                {resistorTabLabel[1]}
              </NavLink>
            </NavItem>
            <NavItem>
              <NavLink
                className={classnames({ active: this.state.activeTab === "4" })}
                onClick={() => { this.toggle("4"); }}
              >
                {resistorTabLabel[2]}
              </NavLink>
            </NavItem>
            <NavItem>
              <NavLink
                className={classnames({ active: this.state.activeTab === "5" })}
                onClick={() => { this.toggle("5"); }}
              >
                {resistorTabLabel[3]}
              </NavLink>
            </NavItem>
            <NavItem>
              <NavLink
                className={classnames({ active: this.state.activeTab === "6" })}
                onClick={() => { this.toggle("6"); }}
              >
                {resistorTabLabel[4]}
              </NavLink>
            </NavItem>
            <NavItem>
              <NavLink
                className={classnames({ active: this.state.activeTab === "7" })}
                onClick={() => { this.toggle("7"); }}
              >
                {resistorTabLabel[5]}
              </NavLink>
            </NavItem>
          </Nav>
          <TabContent activeTab={this.state.activeTab}>
            <TabPane tabId="1">
              <ResistorSearch resistorData={this.state.resistorData} />
            </TabPane>
            <TabPane tabId="2">
              <ResistorContent resistorData={this.filterResistorData("1%, 1/4W")} resistorContainer="resistor-container" />
            </TabPane>
            <TabPane tabId="3">
              <ResistorContent resistorData={this.filterResistorData("5%, 1/4W")} resistorContainer="resistor-container-5-14" />
            </TabPane>
            <TabPane tabId="4">
              <ResistorContent resistorData={this.filterResistorData("5%, 1/2W")} resistorContainer="resistor-container-5-12" />
            </TabPane>
            <TabPane tabId="5">
              <ResistorContent resistorData={this.filterResistorData("5%, 1W")} resistorContainer="resistor-container-5-1" />
            </TabPane>
            <TabPane tabId="6">
              <ResistorContent resistorData={this.filterResistorData("5%, 2W")} resistorContainer="resistor-container-5-2" />
            </TabPane>
            <TabPane tabId="7">
              <ResistorContent resistorData={this.filterResistorData("5%, 5W")} resistorContainer="resistor-container-5-5" />
            </TabPane>
          </TabContent>
          <ScrollUpButton />
        </div>
      );
    }

  }

}

class ResistorSearch extends Component {

  constructor(props) {
    super(props);
    this.state = {
      query: "",
      range: 1.5
    }
  }

  generateResults = () => {
    let query = parseFloat(this.state.query);

    let largeNum = "";
    if (this.state.query.toLowerCase().indexOf("k") != -1 || this.state.query.toLowerCase().indexOf("m") != -1) {
      largeNum = this.state.query.toLowerCase().substring(this.state.query.length - 1);
    }

    let matchData = this.props.resistorData.filter((item) => {
      let match = false;
      let itemValue = parseFloat(item.Value);
      if (query == itemValue || (query > itemValue - this.state.range && query < itemValue + this.state.range)) {
        if (largeNum != "") {
          if (item.Value.toLowerCase().substring(item.Value.length - 1) == largeNum) {
            match = true;
          }
        } else {
          if (item.Value.toLowerCase().substring(item.Value.length - 1) != "m" && item.Value.toLowerCase().substring(item.Value.length - 1) != "k") {
            match = true;
          }
        }
      }
      return (match);
    });

    matchData = matchData.sort(this.compare)

    return (matchData)
  }

  createData = () => {
    let resistorRow = this.generateResults().map((resistor) => {
      return (
        <tr key={resistor.Value + " " + resistor.Type}>
          <td>{resistor.Type}</td>
          <td>{resistor.Value}</td>
          <td>{resistor.Cost}</td>
        </tr>
      );
    });

    return (resistorRow);
  }

  compare = (a, b) => {
    return (parseFloat(a.Value) - parseFloat(b.Value));
  }

  onChange = (event) => {
    this.setState({ query: event.target.value });
  }

  rangeChange = (event) => {
    this.setState({ range: parseFloat(event.target.value) })
  }

  render() {
    let content = null;
    if (this.state.query != "") {
      if (this.createData().length == 0) {
        content = (
          <p className="alert alert-danger text-center">No resistor with that value</p>
        );
      } else {
        content = (
          <table className="table table-striped resistor-table">
            <thead>
              <tr>
                <th>Type</th>
                <th>Value</th>
                <th>Cost</th>
              </tr>
            </thead>
            <tbody>
              {this.createData()}
            </tbody>
          </table>
        );
      }
    }

    return (
      <div>
        <h3 className="text-center mt-3">Search for a Resistor</h3>
        <p className="text-center mt-3 text-secondary small">(Finds resistor within {this.state.range}ohms range)</p>
        <div className="input-group mb-3 minimize">
          <div className="input-group-prepend">
            <span className="input-group-text">Resistance Value: </span>
          </div>
          <input onChange={this.onChange} value={this.state.query} type="text" className="form-control" placeholder="Insert resistance" aria-label="Resistor search input" />
          {/* <div class="input-group-append">
            <span class="input-group-text">Find Resistor within range:</span>
          </div>
          <input onChange={this.rangeChange} value={this.state.range} type="text" className="form-control" placeholder="Specify range" aria-label="Resistor search input" /> */}
        </div>
        {content}
      </div>
    );
  }

}

